import { createSlice } from '@reduxjs/toolkit'

export const name = createSlice({
  name: 'name',
  initialState: {
    value: 'Emil Edberg'
  },
  reducers: {
    pushToCart: (state, action) => {
      state.value.push(action.payload)
    }
  }
})

export const { pushToCart } = name.actions;

export default name.reducer;