import './animations.css';
import './about-me.css';
import { useState } from 'react';
import { useEffect } from 'react';

const AboutMe = () => {
	const [first, setFirst] = useState(false);
	const [second, setSecond] = useState(false);
	const [third, setThird] = useState(false);
	const [fourth, setFourth] = useState(false);
	const [fifth, setFifth] = useState(false);

	useEffect(() => {
		if (!localStorage.getItem('aboutMeIsShowed')) {
			startAnimations()
		} else {
			setFirst(true);
			setSecond(true);
			setThird(true);
			setFourth(true);
			setFifth(true);
		}
	},[])

	function startAnimations() {
		setTimeout(() => {setFirst(true)}, 2000);
		setTimeout(() => {setSecond(true)}, 4000);
		setTimeout(() => {setThird(true)}, 6000);
		setTimeout(() => {setFourth(true)}, 8000);
		setTimeout(() => {
			setFifth(true);
			localStorage.setItem('aboutMeIsShowed', 'true');
		}, 10000);
	}
	return (
		<section className={"about-me-wrapper"}>
			{first ? <h3>"En väldigt målinriktad människa"</h3> : null }
			{second ? <h3>"Snäll, idérik, självständig och bra i samarbeten"</h3>: null }
			{third ? <h3>"En ganska enkel människa med fötterna på jorden"</h3>: null }
			{fourth ? <h3>"Väldigt lätt att lära sig nya saker, med dessutom stor vilja till det"</h3>: null }
			{fifth ? <h3>"Strukturerad, målmedveten och väldigt positiv"</h3>: null }
		</section>
	)
}

export default AboutMe;