import './cv.css'
import './animations.css'
import { useState } from 'react'
import { useEffect } from 'react';
const CV = () => {
	const [first, setFirst] = useState(false);
	const [second, setSecond] = useState(false);
	const [third, setThird] = useState(false);

	useEffect(() => {
		if (!localStorage.getItem('cvIsShowed')){
			startCv();
		}else {
			setFirst(true);
			setSecond(true);
			setThird(true);
		}
		return ()=> {
			setFirst(false);
			setSecond(false);
			setThird(false);
		}
	},[])

	function startCv() {
		setTimeout(() => {setFirst(true)}, 3000);
		setTimeout(() => {setSecond(true)}, 6000);
		setTimeout(() => {
			setThird(true);
			localStorage.setItem('cvIsShowed', 'true');
		}, 9000);
	}
	return (
		<section className="cv-wrapper">
			{third ? <article className="small-info">
				<h2>Profil</h2>
					<p>Jag är en driven och engagerad
							människa med ett tekniskt
							intresse. Har väldigt lätt att lära
							och är bra och lyhörd i team.
							Gillar struktur och enligt många
							känns som en trygg och
							humoristisk person. 
					</p>
			</article> : null}
			
			{second ? 
				<article className="small-info-work education">
					<h2>Kompetenser/Kvalifikationer</h2>
						<ul>
							<li>HTML</li>
							<li>CSS</li>
							<li>Javascript</li>
							<li>Vue</li>
							<li>React</li>
							
						</ul> 
						<ul>
							<li>React-native</li>
							<li>Ljudredigering</li>
							<li>Photoshop</li>
							<li>Musikproduktion</li>
							<li>Live-ljud</li>
						</ul>
				</article> 
			: null}
			{first ? 
				<article className="small-info-work">
					<h2>Utbildning</h2>
						<div>
							<h3>IT-Högskolan Stockholm 2020–2022 (pågående...)</h3>
							<p>Frontend-utveckling</p>
							</div>
						<div>
							<h3>Studio Blue 2009–2011</h3>
							<p>Producent/ljudtekniker</p>
						</div> 
				</article> 
			: null}

			<article className="small-info-work">
				<h2>Arbetslivserfarenhet</h2>
					<div>
						<h3>Emil Edberg egenföretagare 2018–2020</h3>
						<p>
							Jag har arbetat mest som konsult för företag inom bygg.
							Men har också varit ljudtekniker för stora event och TV.
							Samt vid några tillfällen konsultat som konditor. </p>
						</div>
					<div>
						<h3>Bålsta bagaren Konditor/bagare 2014–2018</h3>
						<p>Konditor och Teamleader för ett konditor-team.</p>
					</div> 
			</article>

		</section>
	)
}
export default CV;