import './portfolio.css';
import './animations.css';
import { useEffect } from 'react';
import { useState } from 'react';

const Portfolio = () => {
	const [iframe, setIframe] = useState(false);

	useEffect(() => {
		let screenWidth = window.innerWidth;

		window.addEventListener("resize", () => {
			if (screenWidth && screenWidth < 900) {
				setIframe(false);
				screenWidth = (window.innerWidth);
				return;
			}else {
				setIframe(true);
				screenWidth = window.innerWidth;
				return;
			}
		});

		if (screenWidth && screenWidth < 900) {
			setIframe(false);
			screenWidth = (window.innerWidth);
		}else {
			setIframe(true);
			screenWidth = window.innerWidth;
		}
	}, [setIframe])

	return (
		<section className="portfolio-wrapper">
			<ul>
				<li>
					{iframe ? <iframe title="Listan" src="https://listan.herokuapp.com" className="iframe-listan"></iframe> : <img src="/img/listan.png" className="img" alt="pagepicture" /> }
					<article>
						<h3>Listan</h3>
						<p><br/>
							Det är en simpel list-app som har det lilla extra. Du kan spara listor och använda som tillexempel inhandlings-lista osv. Jag själv använder denna nästan varje dag. Den sparar ner allt via en webserver, enkelt beskrivet så kan min fru uppdatera min inhandlingslista när jag är i butiken och den uppdateras direkt. Behövs inga samtal längre. 
						</p><br />
						{iframe ? <p>Pröva den gärna här till vänster med lösenordet: "guest" </p>
						:
						<p>Pröva den gärna! Gästinloggning så är lösenordet "guest".</p>
						}
						<br /><br />
						<a href="https://listan.herokuapp.com" target="_blank" rel="noreferrer">Öppna i ny flik</a>
					</article>
				</li>
				<li>
				<img src="/img/hamsters.png" className="img" alt="pagepicture" />
				<article>
						<h3>World War Hamsters 2021</h3>
						<p><br/>
						Detta är ett internetspel som har ett eget API för hamstrar som ska kriga mot varandra. Du klickar på den hamster du tycker är coolast, sötast eller vad som.. Du kan se toplistor och se alla fighter om vilken som vunnit vad osv.. 
						</p><br />
						<p>bara testa på om du vill!</p><br />
						
						<a href="https://worldwarhamsters2021.herokuapp.com" target="_blank" rel="noreferrer">Öppna i ny flik</a>
					</article>
				</li>
				<li>
				<img src="/img/starwars.png" className="img" alt="pagepicture" />
					<article>
						<h3>Starwars</h3>
						<p><br/>
						Ett Starwars sida för att kolla Starwars-karaktärers information. Hämtat från ett webAPI som finns på webben.
						</p><br />
						<p>bara gå in och testa på om du vill!</p><br />
						
						<a href="/pages/starwars/index.html" target="_blank" rel="noreferrer">Öppna i ny flik</a>
					</article>
				</li>
				<li>
					<img src="/img/unsplash.png" className="img" alt="pagepicture" />
					<article>
						<h3>Unsplash</h3>
						<p><br/>
						Bildgalleri med sökfunktion från ett webAPI.
						</p><br />
						<p>Gå in och testa på om du vill!</p><br />
						
						<a href="/pages/unsplash/index.html" target="_blank" rel="noreferrer">Öppna i ny flik</a>
					</article>
				</li>
				<li>
					<img src="/img/e-wallet.png" className="img" alt="pagepicture" />
					<article>
						<h3>E-Wallet</h3>
						<p><br/>
						En plånbok på nätet för och spara kortuppgifter.
						</p><br />
						<p>bara gå in och testa på om du vill!</p><br />
						
						<a href="/pages/e-wallet/index.html" target="_blank" rel="noreferrer">Öppna i ny flik</a>
					</article>
				</li>
			</ul>

		</section>
	)
}

export default Portfolio;